import Vue from "vue";
import VueRouter from "vue-router";
import Index from "@/components/Index.vue";
import FingerCulture from "@/components/pages/FingerCulture.vue";
import WebRadio from "@/components/pages/WebRadio.vue";

Vue.use(VueRouter);

export default new VueRouter({
  routes: [
    {
      path: "/",
      name: "Index",
      component: Index
    },
    {
      path: "/fingerculture",
      name: "FingerCulture",
      component: FingerCulture
    },
    {
      path: "/webradio",
      name: "WebRadio",
      component: WebRadio
    }
  ],
  scrollBehavior() {
    return { x: 0, y: 0 };
  }
});
